import React from "react";
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider'
import { StylesProvider, createGenerateClassName, jssPreset } from '@material-ui/styles'
import { create } from 'jss'
import { theme, Header } from "unicef-material-ui";
import { NavLinks, MainContent } from '../components'

const generateClassName = createGenerateClassName({
  dangerouslyUseGlobalCSS: false,
	productionPrefix: 'c',
});

const jss = create({
	...jssPreset(),
	// Define a custom insertion point that JSS will look for when injecting the styles into the DOM.
	insertionPoint: 'jss-insertion-point',
});


export default function App() {

  return (
    <MuiThemeProvider theme={theme}>
      <StylesProvider generateClassName={generateClassName}>
        <StylesProvider jss={jss}>
          <Header
            applicationName="Material UI"
            navLinks={<NavLinks />}
          //   tabs={<NavTabs tabs={tabs} />}
          //   // hideLogo={false}
          //   // logoBorderLine={false}
          //   // logo={<img alt="user" src={avatar} />}
          //   menuItems={<MenuItems />}
          //   menuTabs={<MenuTabs />}
            menuButton={true}
          />
          <MainContent />
        </StylesProvider>
		  </StylesProvider>
    </MuiThemeProvider >
  )
}
